import React from 'react';
import { Provider } from 'react-redux';
import { HashRouter } from 'react-router-dom';
import App from './App';

//root exists to wrap App component (top-level) in a Provider

const Root = ({ store }) => (
  <Provider store={store}>
    <HashRouter>
    <App />
    </HashRouter>
  </Provider>
);


export default Root;



